* {
    margin: 0;
    border: 0;
  }
  

  thead {
      box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2);
  }
  
  thead th {
  
    text-align: center;
    width: 100%;
  
  }
  
  header {
    background: #fff;
    padding: 0.5rem;
    color: rgba(0, 0, 0, 0.7);
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-radius: 8px 8px 0px 0px;
  }
  
  header h1 {
   font-size: 1.25rem;
   text-align: center;
   font-weight: normal;
  
  }
  tbody {
      position: relative;
      top: 40px;
  }
  table {
    background: #fff;
    width: 100%;
    height: 100%;
    border-collapse: collapse;
    table-layout: fixed;
  
  }
  
  
  .headcol {
    width: 80px;
    font-size: 0.875rem;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.5);
    padding: 0.25rem 0;
    text-align: center;
    border: 0;
    position: relative;
    /*top: -12px;*/
    vertical-align: middle;
    border-bottom: 1px solid transparent;
  }
  
  thead th {
    font-size: 1rem;
    font-weight: bold;
    color: black;
    padding: 0.5rem;
  }
  
  thead {
      z-index: 2;
      background: white;
      border-bottom: 2px solid #ddd;
  
  }
  
  tr, tr td {
    height: 20px;
  }
  td {
    text-align: center;
  }
  tr:nth-child(odd) td:not(.headcol) {
    border-bottom: 1px solid #e8e8e8;
  }
  
  tr:nth-child(even) td:not(.headcol) {
    border-bottom: 1px solid #eee;
  }
  
  tr td {
    border-right: 1px solid #eee;
    padding: 0;
    white-space: none;
    word-wrap: nowrap;
  }
  
  tbody tr td {
    position: relative;
    vertical-align: top;
    height: 40px;
    padding: 0.25rem 0.25rem 0 0.25rem;
    width: auto;
  
  }
  
  .checkbox {
     display: none;
  }
  
  .checkbox + label {
      border: 0;
      outline: 0;
      width: 100px;
      height: 100px;
      background: white;
      color: transparent;
      display:block;
    display: none;
  }
  
  .checkbox:checked + label {
      border: 0;
      outline: 0;
      width: 100%;
      height: 100%;
      background: red;
      color: transparent;
      display: inline-block;
  }
  
  .event {
    background: #00B4FC;
    color: white;
    border-radius: 2px;
    text-align: left;
    font-size: 0.88rem;
    z-index: 2;
    padding: 0.5rem;
    overflow-x: hidden;
    transition: all 0.2s;
    cursor: pointer;
  }
  
  .event:hover {
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
    /*background: #00B4FC;*/
  }
  
  .event.double {
    height: 200%;
  }
  
  
  td:hover:after {
    /*content: "+";*/
    vertical-align: middle;
    font-size: 1.875rem;
    font-weight: 100;
    color: rgba(0, 0, 0, 0.5);
    position: absolute;
  }
  
  button.secondary {
    border: 1px solid rgba(0, 0, 0, 0.1);
    background: white;
    padding: 0.5rem 0.75rem;
    font-size: 14px;
    border-radius: 2px;
    color: rgba(0, 0, 0, 0.5);
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    font-weight: 500;
  }
  
  button.secondary:hover {
    background: #fafafa;
  }
  button.secondary:active {
    box-shadow: none;
  }
  button.secondary:focus {
    outline: 0;
  }
  
 .past {
    background: #fafafa;
  }
  
  .today {
    color:yellow;
    background-color: #00B4FC;
  }

  .secondary {
    color: rgba(0, 0, 0, 0.4);
    background-color: white;
  }

  .today.secondary {
    background-color: #0092cc76;
  }
  
  .now {
    box-shadow: 0px -1px 0px 0px red;
  }
  
  .icon {
    font-size: 1.5rem;
    margin: 0 1rem;
    text-align: center;
    cursor: pointer;
    vertical-align: middle;
    position: relative;
    top: -2px;
  }
  
  .icon:hover {
    color: red;
  }

  .outer {
    position:relative;
  }
  
  .calendar {
      margin: 0 auto;
    max-width: 1280px;
    min-width: 500px;
  
    box-shadow: 0px 30px 50px rgba(0, 0, 0, 0.2) ,0px 3px 7px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
  }
  .wrap {
  
    overflow-x: hidden;
    overflow-y: scroll;
      max-width: 1280px;
    height: 640px;
    border-radius: 8px;
  }

  .calendar_title{
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .secondary_header{
    align-self: flex-start;
    flex: 0 0 1;
    min-width: 250px;
  }
  .cell{
    text-align: center;
    border-radius: 5px;
    padding: 1px;
    box-shadow:  2px 2px 4px #555555;
  }
  .lunch{
    background-color: mediumvioletred;
  }

  .homeroom {
    background-color: darkblue;
  }

  .math {
    background-color: limegreen;
  }

  .social{
    background-color:firebrick;
  }

  .ela {
    background-color: goldenrod;
  }

  .science {
    background-color: #000077;
  }

  .phys{
    background-color: rgb(241, 105, 8);
  }

